img {
    width: 15vw;
}

.intro {
    position: relative;
    top:80px;
}

#intro {
    position: relative;
    top:-100px;
}

.hello{
    display: flex;
    justify-content: space-around;
    padding: 80px;
    background-color: #FDEFF5 ;
}

strong {
    font-size: 33px;
}

h2 {
    text-align: center;
    padding-top: 40px;
    font-size: 25px;
    color: #A6487F
}

.btn {

    background-color: #A6487F;
    color: #FDEFF5;
    padding: 0.625rem 1.375rem;
    font-size: 1.125rem;
    border: none;
    border-radius: 50px;
    margin-top: 0.7em;
    display: block;
    width: fit-content;
    cursor: pointer;
    margin: 0.7em auto;
}

.photo {
    width: 250px;
    height: 250px;
    border-radius: 50%;
  }

.github, .linkedin {
    width:45px;
}

.socials {
    text-align: center;
    padding-left: 0;
}

.about {
 padding-left: 10%;
 padding-right: 10%;
}

@media (max-width: 1000px) {
.hello {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
}

@media (max-width: 700px) {
 strong {
    font-size: 25px;}

    .hello {
        padding-left: 40px;
        padding-right: 40px;
    }
 }


 @media (max-width: 470px) {
    strong {
       font-size: 18px;
    }

    .hello {
        padding-left: 20px;
        padding-right: 20px;
    }
   
   }
}





